.companyInfoModal {
  position: absolute;
  z-index: 100;
  background-color: #f5f5f5;
  left: 30%;
  top: 20%;
  padding: 20px;
  border-left: 3px solid #E41A38;
  border-right: 3px solid #111111;
  min-width: 150px;
  max-width: 200px;
  /* box-shadow: 3px 3px 0 black; */
}
.copmanyInfoModalHeader {
  text-decoration: underline;
  font-weight: 500;
}
.companyInfoModal.content {
  position: relative;
  width: 100%;
  height: 100%;
}
.companyInfoModal .close-btn {
  position: absolute;
  padding: 3px;
  right: 0;
  top: 0;
  font-size: 1.1em;
  background-color: #fff0;
  border: none;
  text-shadow: 0 1px 0 #fff;
}
.companyInfoModal .close-btn img {
  width: 20px;
  height: auto;
  border: none;
}
.companyInfoModal .close-btn img:hover {
  transform: scale(1.1)
}
.companyInfoModal .close-btn:hover {
  font-weight: 700;
  cursor: pointer;
}
.companyInfoModal .close-btn:focus {
  outline: none;
}

.companyInfoModal .imageContainer {
  overflow: hidden;
}
.companyInfoModal .imageContainer img {
  width: 100%;
  height: auto;
}

.companyInfoModal .attribute-descr {
  color: #7f7f7f;
  font-style: italic;
}

.companyInfoModal p {
  text-align: left;
}

@media(max-width: 600px) {
  .companyInfoModal {
    left: 10%;
    right: auto;
    top: 30%;
  }
}
