.footer {
  padding: 10px 20px;
  text-align: start;
  background-color: #dfdfdf;
}
.footer-header {
  text-align: center;
}
.footer-item {
  display: inline-flex;
  align-items: center;
  width: 30%;
}
.footer-item img{
  width: 30px;
  height: auto;
  border: 2px solid black;
  border-radius: 3px;
  margin-right: 10px;
}

@media (max-width: 600px) {
  .footer-item {
    width: 50%;
  }
}